.contacts-body {
    padding: 10px;
}

.contacts-body-heading {
    font-size: 30px;
    font-weight: 800;
    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 3px;
        background-color: $blue-sky;
    }
}


.contacts-body-contacts {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    &>* {
        margin-top: 5px;
    }
}

.contacts-body-link {
    color: $black-color;
    text-decoration: none;
    transition: .5s ease-in;
    &:hover {
        color: $blue-sky;
    }
}

.contacts-body-map {
    margin-top: 5vh;
}