.news-wrapper {
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 10vh auto 0;
}

.news-wrapper-col {
    position: relative;
    z-index: 1;
    width: 40%;
    height: 30vh;
    border: 1px solid $black-color;
    padding: 4%;
}

.news-wrapper-heading {
    color: $white-color;
}

.news-wrapper-uppercase {
    text-transform: uppercase;
    font-weight: 800;
}

.news-wrapper-col-first, .news-wrapper-col-second {
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
  
    &:after {
        content: "";
        position: absolute;
        z-index: 1;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: $half-dark-blue;
        opacity: .5;
    }
}

.news-wrapper-col-first {
    background-image: url("../img/general/news-images/news-first-bg.png");
}

.news-wrapper-col-second {
    background-image: url("../img/general/news-images/news-second-bg.png");
}


.news-wrapper-col-text {
    position: absolute;
    z-index: 4;
}

.news-wrapper-link {
    text-transform: uppercase;
    color: $sea-color;
    &:hover {
        color: $black-color;
        text-decoration: none;
    }
}

.news-wrapper-paragraph {
    color: $white-color;
}
