.projects-figure {
  position: relative;
  width: 80%;
  margin: 5vh auto;
}


.project-figcaption {
  position: absolute;
  bottom: 1%;
  width: 40%;
  padding: 40px;
  background-color: $black-color;
  transition: .4s ease;
  
  &:hover {
    background-color: $white-color;
	
    .projects-link {
      color: $black-color;
      text-decoration: none;
    }
  }
  
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: -27%;
    padding: 63px 70px;
    background-color: $blue-sky;
  }
  
  .projects-link {
    font-size: 20px;
    color: $white-color;
    transition: .4s ease;
  }
}

