.header {
    height: 100vh;
    background-image: url("../img/general/header-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
}

.header-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-nav {
    width: 40%;
}

.header-list {
    display: flex;
    justify-content: space-between;
    list-style: none;
}


.header-item {
    position: relative;
}

.header-link {
    color: $white-color;
    text-decoration: none;
    transition: ease .5s;
  
    &:hover {
        color: $blue-sky;
	
        &:before {
            content: "";
            position: absolute;
            top: -50%;
            width: 100%;
            height: 3px;
            background-color: $blue-sky;
        }
    }
}


.header-text-block {
    width: 50%;
    margin: 30vh auto 0;
    color: $white-color;
    text-align: center;
}


.text-block-heading {
    font-weight: 700;
    font-size: 36px;
}

.text-block-paragraph {
    font-size: 20px;
}


.header-bottom {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: 15vh auto 0;
}

.bottom-col {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30%;
    border: 1px solid $black-color;
    padding: 10px;
    background-color: $white-color;
}

.bottom-col-paragraph {
    font-size: 1.5rem;
}
