.catalog-body {
    padding: 2vh 5vw;
    margin-top: 10vh;
}

.catalog-body-heading {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: .2rem;
        background-color: $blue-sky;
    }
}

.catalog-types {
    margin-top: 5vh;
}

.catalog-col-wrapper {
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 2% auto 0;
}


.catalog-types-item {
    list-style: "& ";
    margin-top: 20px;
}

.catalog-types-menu-item {
    list-style: none;
}

.catalog-types-span {
    cursor: pointer;
    position: relative;
    margin-top: 10px;
}

.catalog-types-menu {
    display: none;
    padding: 2%;
    background-color: $white-gray-color;
    box-shadow: 10px 10px 3px $white-gray-color;

    &.visible-class {
        display: block;
    }
}