.footer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: auto;
  background-image: url("../img/general/footer-bg.png");
  background-size: contain;
  color: $white-color;
  padding: 3%;
  margin-top: 5vh;
}


.footer-menu-list {
  display: flex;
  flex-direction: column;
  list-style: none;
}

.footer-menu-link {
  color: $white-color;
  font-size: 14px;
  text-decoration: none;
  transition: .3s ease;
  
  &:hover {
    color: $blue-sky;
  }
}

.footer-mail-link {
  color: $white-color;
}

.footer-contacts {
  width: 30%;
}
