.project-tabs {
    padding: 5vw;
}

.project-tabs-buttons {
    position: relative;
    &::before {
        content: "";
        position: absolute;
        bottom: -12%;
        width: 100%;
        height: 3px;
        background-color: $blue-sky;
    }
}

.project-tabs-button {
    position: relative;
    padding: 20px;
    border-radius: 10px;
    outline: none;
    background-color: $white-color;
    transition: .4s ease;
    &.button-bg {
        color: $blue-sky;
    }
}

.project-tabs-content {
    display: none;
    &.tab-active {
        display: block;
    }
}

.project-tabs-pagination {
    margin-top: 5vh;
}