.news-text {
    padding: 20px;
}

.news-text-heading {
    text-transform: capitalize;
    font-weight: 700;
    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $blue-sky;
    }
}

.news-text-content {
    display: none;

    &.visible-text {
        display: block;
    }
}


.news-text-buttons {
    display: flex;
    justify-content: space-between;
    width: 40%;
    margin: 10vh auto 0;
}

.news-text-button {
    padding: 10px 25px;
    border-radius: 5px;
    outline: none;
    font-size: 18px;

    &.news-bg-color {
        background-color: $red-color;
    }
}