.top-heading {
    position: relative;
    width: 20%;
    margin: auto;
    padding-top: 10vh;
    text-align: center;
    font-size: 2rem;
    color: $black-color;
    font-weight: 700;
  
    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: -5%;
        width: 110%;
        height: .23rem;
        background-color: $blue-sky;
    }
}


.production-wrapper {
    margin-top: 10vh;
    background-image: url("../img/general/production-images/production-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.production-top, .production-bottom {
    display: flex;
    justify-content: space-between;
    padding: 2%;
}

.bottom-button {
    display: block;
    margin: 10vh auto 0;
    border: 1px solid $black-color;
    padding: 1%;
    transition: .4s ease;
    color: $black-color;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    outline: none;
  
    &:hover {
        background-color: $black-color;
        color: $white-color;
    }
}
