.heads {
    background-color: $half-dark-blue;
}

.heads-heading {
    position: relative;
    width: 20%;
    margin: auto;
    padding-top: 30vh;
    text-align: center;
    font-size: 2rem;
    color: $white-color;
    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: -5%;
        width: 110%;
        height: .23rem;
        background-color: $blue-sky;
    }
}

.heads-persons {
    display: flex;
    justify-content: space-between;
    padding-top: 10vh;
    width: 70%;
    margin: auto;
}

.heads-figure {
    margin-top: 0;
    color: $white-color;
}


.paragraph-24 {
    font-size: 24px;
}

.paragraph-20 {
    font-size: 20px;
}
